*{
  margin: 0;
}
.top-pane{
  background-color: hsl(225, 6%, 25%);
}
.pane{
  display: flex;
  height: 50vh;
}
.editor-container{
  flex-grow: 1;
  flex-basis: 0;
  flex-direction: column;
  display: flex;
  padding: .5rem;
  background-color: hsl(225, 6%, 25%);
}
.editor-container.collapsed{
  flex-grow: 0;
}
.editor-container.collapsed .CodeMirror-scroll{
position:absolute;
overflow: hidden !important;
}
.editor-title{
  display: flex;
  justify-content: space-between;
  background-color: hsl(225, 6%, 13%);
  color: white;
  padding: .5rem .5rem .5rem .1rem;
  border-top-right-radius: .5rem;
  border-top-left-radius: .5rem;
}
.CodeMirror{
  height: 100% !important;
}
.code-mirror-wrapper{
  border-top-right-radius: .5rem;
  border-top-left-radius: .5rem;
  flex-grow: 1;
  overflow: hidden;
}
.expand-collapse-btn{
  margin-left: .5rem;
  border: none;
  background: none;
  color: white;
  cursor: pointer;
}